import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

const router = new Router({
  linkActiveClass: 'active',
  base: process.env.BASE_URL,
  routes: [
    {path: '/', component: () => import(`./views/login/Index.vue`)},
    {path: '/login', component: () => import(`./views/login/Index.vue`)},
    /* 指挥调度中心 */
    {
      path: '/command', component: () => import('./views/Index.vue'),
      children: [
        {path: '/command/info', component: () => import(`./views/command/Info.vue`)},// 信息展示
        {path: '/command/org', component: () => import(`./views/command/Org.vue`)},// 信息展示
        {path: '/command/role', component: () => import(`./views/command/Role.vue`)},// 信息展示
      ]
    },
    {path: '*', redirect: {path: '/command/info'}}
  ]
})
export default router
